const spermTooLowCountValidator = (complications) =>
    helpers.withParams({ type: "form" }, (value) => {
        if (complications.value.length == 0) {
            return true;
        }

        if (complications.value.includes("None")) {
            return helpers.req(value);
        }
        return true;
    });

const typeOfPregnancyValidator = (embryoTransfer) =>
    helpers.withParams({ type: "embryoTransfer" }, (value) => {
        if (embryoTransfer.value == "no") {
            return value === "Not Pregnant";
        }

        if (embryoTransfer.value == "yes") {
            return helpers.req(value);
        }
        return true;
    });

const fetalSacsUSValidator = (param) =>
    helpers.withParams({ type: "form" }, (value) => {
        if (
            param.typeOfPregnancy == null ||
            param.typeOfPregnancy == "Biochemical" ||
            param.typeOfPregnancy == "Not Pregnant" ||
            param.typeOfPregnancy == "Unknown"
        ) {
            return true;
        }

        if (
            param.typeOfPregnancy == "Clinical Intrauterine" ||
            param.typeOfPregnancy == "Heterotopic" ||
            param.typeOfPregnancy == "Molar Pregnancy"
        ) {
            return parseInt(value) > 0;
        }

        if (param.typeOfPregnancy == "Ectopic") {
            return parseInt(value) >= 0;
        }

        return true;
    });

const fetalheartsUSValidator = (param) =>
    helpers.withParams({ type: "form" }, (value) => {
        if (
            param.typeOfPregnancy == "Clinical Intrauterine" ||
            param.typeOfPregnancy == "Heterotopic"
        ) {
            return helpers.req(value);
        }

        return true;
    });

const chorionicityValidator = (typeOfPregnancy, fetalheartsUS) =>
    helpers.withParams({ type: "form" }, (value) => {
        if (
            (typeOfPregnancy.value == "Clinical Intrauterine" ||
                typeOfPregnancy.value == "Heterotopic") &&
            parseInt(fetalheartsUS.value) >= 2
        ) {
            return helpers.req(value);
        }

        return true;
    });

const treatmentOutcomeValidator = (embryoTransfer, cycleCancelled) =>
    helpers.withParams({ type: "form" }, (value) => {
        if (embryoTransfer.value == "no" || cycleCancelled.value == "yes") {
            return value == "Cycle not completed to transfer" || value == null;
        }

        return true;
    });

export function useComplicationFormInit() {
    const toast = useToast();
    const store = useStore();
    const confirm = useConfirm();

    const form = reactive({
        complications: [],
        spermTooLowCount: null,
        treatmentOutcome: null,
        typeOfPregnancy: null,
        fetalSacsUS: null,
        fetalheartsUS: null,
        chorionicity: null,
        electiveFetalReduction: null
    });
    const loading = ref(false);
    const complications = computed(() => store.state.born.complications);

    const { typeOfPregnancy, fetalheartsUS } = toRefs(form);

    const fillForm = (data) => {
        form.complications = data.complications ? data.complications : [];
        form.spermTooLowCount = data.spermTooLowCount;
        form.fetalSacsUS = data.fetalSacsUS;
        form.fetalheartsUS = data.fetalheartsUS;
        form.chorionicity = data.chorionicity;
        form.electiveFetalReduction = data.electiveFetalReduction;
        if (data.treatmentOutcome != null) {
            form.treatmentOutcome = data.treatmentOutcome;
        }
        if (data.typeOfPregnancy != null) {
            form.typeOfPregnancy = data.typeOfPregnancy;
        }
    };

    const rule = {
        complications: {
            required
        },
        spermTooLowCount: {
            validator: helpers.withMessage(
                "Value Required",
                spermTooLowCountValidator(computed(() => form.complications))
            )
        },
        typeOfPregnancy: {
            validator: helpers.withMessage(
                "Value Required if embryo transfer is yes & must be Not Pregnent if embryo transfer is no",
                typeOfPregnancyValidator(
                    computed(() => store.state.born.dependentOn.embryoTransfer)
                )
            )
        },
        fetalSacsUS: {
            validator: helpers.withMessage(
                "Value Required",
                fetalSacsUSValidator(form)
            )
        },
        fetalheartsUS: {
            validator: helpers.withMessage(
                "Value Required",
                fetalheartsUSValidator(form)
            )
        },
        chorionicity: {
            validator: helpers.withMessage(
                "Value Required",
                chorionicityValidator(typeOfPregnancy, fetalheartsUS)
            )
        },
        electiveFetalReduction: {
            validator: helpers.withMessage(
                "Value Required",
                chorionicityValidator(typeOfPregnancy, fetalheartsUS)
            )
        },
        treatmentOutcome: {
            validator: helpers.withMessage(
                "Value Must be Cycle not completed to transfer or null",
                treatmentOutcomeValidator(
                    computed(() => store.state.born.dependentOn.embryoTransfer),
                    computed(() => store.state.born.dependentOn.cycleCancelled)
                )
            )
        }
    };

    const v$ = useVuelidate(rule, form);

    const _save = () => {
        loading.value = true;
        const data = {
            ...form,
            complications: form.complications.join(","),
            ...store.getters["artCycle/getSelectedCycleAndPatientId"]
        };

        store
            .dispatch("born/updateComplication", data)
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Save Successful",
                    life: 3000
                });
            })
            .catch((err) => {})
            .finally(() => (loading.value = false));
    };
    const save = () => {
        if (v$.value.$invalid) {
            return;
        }
        _save();
    };

    const fetalSacsUSWarning = ref();

    const complicationOptions = computed(
        () => store.state.options.complications
    );
    const treatmentOutcomeOptions = computed(
        () => store.state.options.treatmentOutcome
    );
    const typeOfPregnancyOptions = computed(
        () => store.state.options.typeOfPregnancy
    );
    const chorionicityOptions = computed(
        () => store.state.options.chorionicity
    );
    const electiveFetalReductionOptions = computed(
        () => store.state.options.electiveFetalReduction
    );

    return {
        store,
        form,
        save,
        v$,
        complications,
        fillForm,
        loading,
        confirm,
        complicationOptions,
        treatmentOutcomeOptions,
        typeOfPregnancyOptions,
        chorionicityOptions,
        electiveFetalReductionOptions
    };
}

