export function usePregnancyOutcomeForm() {
    const store = useStore();
    const toast = useToast();
    const form = reactive({
        pregnancyBirthOutcome: null,
        newbornDateBirth: null,
        pregnancyLossDate: null,
        babiesborn: null,
        gestationalWeeks: null,
        gestationalDays: null
    });
    const loading = ref(false);

    const fillForm = (data) => {
        form.pregnancyBirthOutcome = data.pregnancyBirthOutcome;
        form.newbornDateBirth = data.newbornDateBirth;
        form.pregnancyLossDate = data.pregnancyLossDate;
        form.babiesborn = data.babiesborn;
        form.gestationalWeeks = data.gestationalWeeks;
        form.gestationalDays = data.gestationalDays;
    };

    const rules = {
        pregnancyLossDate: {
            validator: helpers.withMessage(
                "Value Required",
                pregnancyLossDateValidator(form)
            )
        },
        newbornDateBirth: {
            validator: helpers.withMessage(
                "Value Required",
                newbornDateBirthValidator(form)
            )
        },
        babiesborn: {
            validator: helpers.withMessage(
                "Value Required",
                babiesbornValidator(form)
            )
        }
    };

    const v$ = useVuelidate(rules, form);

    const _save = () => {
        loading.value = true;
        const data = {
            ...form,
            ...store.getters["artCycle/getSelectedCycleAndPatientId"],
            newbornDateBirth: form.newbornDateBirth
                ? format(new Date(form.newbornDateBirth), DATE_FORMAT_STRING)
                : null,
            pregnancyLossDate: form.pregnancyLossDate
                ? format(new Date(form.pregnancyLossDate), DATE_FORMAT_STRING)
                : null
        };

        store
            .dispatch("born/updatePregnancyOutcome", data)
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Save Successful",
                    life: 3000
                });
            })
            .catch((err) => {})
            .finally(() => (loading.value = false));
    };

    const confirm = useConfirm();
    const save = () => {
        if (v$.value.$invalid) {
            return;
        }
        _save();
    };

    const pregnancyBirthOutcomeOptions = computed(
        () => store.state.options.pregnancyBirthOutcome
    );

    return {
        store,
        form,
        v$,
        pregnancyBirthOutcomeOptions,
        loading,
        fillForm,
        save
    };
}

const isNumberOocytesCryoedIsGreaterThan0 = (param) =>
    helpers.withParams({ type: "form" }, (value) => {
        if (parseInt(param.oocyteCryoPreserved) > 0) {
            return helpers.req(value);
        }
        return true;
    });

const spermOriginValidator = (oocytesInseminatedInjected) =>
    helpers.withParams({ type: "inserminatedInjected" }, (value) => {
        if (parseInt(oocytesInseminatedInjected.value) > 0) {
            return helpers.req(value);
        }
        return true;
    });

const spermSourceValidator = (oocytesInseminatedInjected) =>
    helpers.withParams({ type: "inserminatedInjected" }, (value) => {
        if (parseInt(oocytesInseminatedInjected.value) > 0) {
            return helpers.req(value);
        }
        return true;
    });

const isIVForFETCycle = (param) =>
    helpers.withParams({ type: "cycleType" }, (value) => {
        return true; // temp for now
        if (param == "IVF" || param == "FET") {
            return value != null;
        }
        return true;
    });

const isOocyteRetrivedGreaterThan0 = (param) =>
    helpers.withParams({ type: "form" }, (value) => {
        // return true; // temp for now
        if (parseInt(param.retrievedOrThawed) > 0) {
            return helpers.req(value);
        }
        return true;
    });

const spermCountValidator = (spermTooLowCount) =>
    helpers.withParams({ type: "spermTooLowCond" }, (value) => {
        if (spermTooLowCount.value == "N") {
            return value >= 1 && value <= 999;
        }
        return true;
    });

const semenVolumeValidator = (spermOrigin, spermSource, cyccleType) =>
    helpers.withParams({ type: "semenValume" }, (value) => {
        if (cyccleType.value == "Egg Freezing") return true;

        let intersection = [];
        let freshSpermOrigins = ["SPM005", "SPM007", "SPM004"];
        intersection = spermOrigin.value.filter((item) =>
            freshSpermOrigins.includes(item)
        );

        if (spermSource.value == "SSRC001" && intersection.length > 0) {
            return helpers.req(value) && value >= 0.1 && value <= 15;
        }

        return true;
    });

export function useOocyteCollectionInit() {
    const toast = useToast();
    const store = useStore();
    const form = reactive({
        oocyteCollectionDate: null,
        oocyteThawDate: null,
        collectionDateUnknown: null,
        oocyteFreezeID1Thawed: null,
        oocyteFreezeID2Thawed: null,
        retrievedOrThawed: null,
        oocyteCryoPreserved: null,
        oocyteFreezeID: null,
        donorProviderIDCode: null,
        donorProviderSource: null,
        oocyteFreezeDate: null,
        oocyteOrigin: null,
        donorProviderstatus: null,
        oocyteCryoMethod: null,
        spermOrigin: null,
        ageSpermProvider: null,
        spermDonorID: null,
        spermDonorStatus: null,
        spermMotility: null,
        spermTooLowCount: null,
        spermDonorBank: null,
        spermSource: null,
        semenVolume: null,
        spermCount: null,
        spermCollection: null
    });
    const oocyteCollections = computed(
        () => store.state.born.oocyteCollections
    );

    const loading = ref(false);
    const rules = {
        oocyteCollectionDate: {
            validator: helpers.withMessage(
                "Value Required",
                isIVForFETCycle(store.state.artCycle.selectedCycle.cycleType)
            )
        },
        oocyteCryoPreserved: { required },
        oocyteCryoMethod: {
            validator: helpers.withMessage(
                "Value Required",
                isNumberOocytesCryoedIsGreaterThan0(form)
            )
        },
        spermOrigin: {
            validator: helpers.withMessage(
                "Value Required",
                spermOriginValidator(
                    computed(
                        () =>
                            store.state.born.dependentOn
                                .oocytesInseminatedInjected
                    )
                )
            )
        },
        spermSource: {
            validator: helpers.withMessage(
                "Value Required",
                spermSourceValidator(
                    computed(
                        () =>
                            store.state.born.dependentOn
                                .oocytesInseminatedInjected
                    )
                )
            )
        },
        oocyteFreezeDate: {
            validator: helpers.withMessage(
                "Value Required",
                isNumberOocytesCryoedIsGreaterThan0(form)
            )
        },
        spermCount: {
            validator: helpers.withMessage(
                "Value must be between 1 to 999",
                spermCountValidator(computed(() => form.spermTooLowCount))
            )
        },
        oocyteOrigin: { required },
        retrievedOrThawed: { required },
        semenVolume: {
            validator: helpers.withMessage(
                `Applicable if SpermOrigin = 'Fresh donor sperm',
                'Fresh partner sperm' or 'Fresh own sperm'
                and SpermSource = 'Semen'.
                Range: 0.1 - 15 mL.`,
                semenVolumeValidator(
                    computed(() => form.spermOrigin),
                    computed(() => form.spermSource),
                    computed(() => store.state.artCycle.selectedCycle.cycleType)
                )
            )
        }
    };
    const v$ = useVuelidate(rules, form);

    const _save = () => {
        loading.value = true;
        const data = {
            ...form,
            oocyteCollectionDate: form.oocyteCollectionDate
                ? format(
                      new Date(form.oocyteCollectionDate),
                      DATE_FORMAT_STRING
                  )
                : null,
            oocyteThawDate: form.oocyteThawDate
                ? format(new Date(form.oocyteThawDate), DATE_FORMAT_STRING)
                : null,
            oocyteFreezeDate: form.oocyteFreezeDate
                ? format(new Date(form.oocyteFreezeDate), DATE_FORMAT_STRING)
                : null,
            ...store.getters["artCycle/getSelectedCycleAndPatientId"]
        };

        store
            .dispatch("born/updateOocyteCollection", data)
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Save Successful",
                    life: 3000
                });
                // fillForm(store.state.born.oocyteCollections);
            })
            .finally(() => (loading.value = false));
    };
    const confirm = useConfirm();
    const save = () => {
        if (v$.value.$invalid) {
            return;
        }
        _save();
    };

    const fillForm = (data) => {
        form.oocyteCollectionDate = data.oocyteCollectionDate
            ? new Date(data.oocyteCollectionDate + TIME_0)
            : null;
        form.oocyteThawDate = data.oocyteThawDate
            ? new Date(data.oocyteThawDate + TIME_0)
            : null;
        form.collectionDateUnknown = data.collectionDateUnknown;
        form.oocyteFreezeID1Thawed = data.oocyteFreezeID1Thawed;
        form.oocyteFreezeID2Thawed = data.oocyteFreezeID2Thawed;
        form.retrievedOrThawed = data.retrievedOrThawed;
        form.oocyteFreezeID = data.oocyteFreezeID;
        form.donorProviderIDCode = data.donorProviderIDCode;
        form.donorProviderSource = data.donorProviderSource;
        form.oocyteFreezeDate = data.oocyteFreezeDate;
        form.oocyteOrigin = data.oocyteOrigin;
        form.donorProviderstatus = data.donorProviderstatus;
        form.oocyteCryoMethod = data.oocyteCryoMethod;
        form.oocyteCryoPreserved = data.oocytesCryopreserved;

        if (store.state.artCycle.selectedCycle.cycleType != "Egg Freezing") {
            form.spermOrigin = data.spermOrigin;
            form.ageSpermProvider = data.ageSpermProvider;
            form.spermDonorID = data.spermDonorID;
            form.spermDonorStatus = data.spermDonorStatus;
            form.spermMotility = data.spermMotility;
            form.spermTooLowCount = data.spermTooLowCount;
            form.spermDonorBank = data.spermDonorBank;
            form.spermSource = data.spermSource;
            form.semenVolume = data.semenVolume;
            form.spermCount = data.spermCount;
        }
    };

    const oocyteOriginOptions = computed(
        () => store.state.options.oocyteOrigin
    );
    const donorProviderStatusOptions = computed(
        () => store.state.options.donorProviderstatus
    );
    const oocyteCryoMethodOptions = computed(
        () => store.state.options.oocyteCryoMethod
    );
    const spermOriginOptions = computed(() => store.state.options.spermOrigin);
    const spermOriginStatusOptions = computed(
        () => store.state.options.spermDonorStatus
    );
    const spermSourceOptions = computed(() => store.state.options.spermSource);

    return {
        store,
        form,
        loading,
        v$,
        save,
        fillForm,
        oocyteCollections,
        oocyteOriginOptions,
        donorProviderStatusOptions,
        oocyteCryoMethodOptions,
        spermOriginOptions,
        spermOriginStatusOptions,
        spermSourceOptions
    };
}

