const embryosTransferredValidator = (
    embryoTransfer,
    utilizableEmbryos,
    embryosCryopreserved,
    utilizableAfterThaw
) =>
    helpers.withParams({ type: "form" }, (value) => {
        let _utilizableEmbryos =
            utilizableEmbryos.value != null
                ? parseInt(utilizableEmbryos.value)
                : 0;
        let _utilizableAfterThaw =
            utilizableAfterThaw.value != null
                ? parseInt(utilizableAfterThaw.value)
                : 0;
        let _embryosCryopreserved =
            embryosCryopreserved.value != null
                ? parseInt(embryosCryopreserved.value)
                : 0;

        let calc =
            _utilizableEmbryos + _utilizableAfterThaw - _embryosCryopreserved;

        if (embryoTransfer.value == "yes") {
            return helpers.req(value) && parseInt(value) <= calc;
        }
        return true;
    });

