export function useBornFreshEmbryoInit() {
    const store = useStore();
    const toast = useToast();
    const loading = ref(false);
    const form = reactive({
        inseminationMethod: [],
        oocytesNotUsed: null,
        reasonsForNoUse: [],
        embryoStageFreezing: [],
        inseminatedInjected: null,
        embryoFreezeID1: null,
        embryoFreezeID2: null,
        embryoFreezeID3: null,
        embryoFreezeID4: null,
        normalFertilization2PN: null,
        embryoFreezeDate1: null,
        embryoFreezeDate2: null,
        embryoFreezeDate3: null,
        embryoFreezeDate4: null,
        ofMII: null,
        utilizableEmbryos: null,
        embryosCryopreserved: null,
        reasonEmbryoCryopreservation: null,
        embryoFreshCryoMethod: null
    });

    const rules = {
        inseminationMethod: {
            validator: helpers.withMessage(
                "Value Required",
                inseminationMethodValidator(
                    computed(
                        () => store.state.born.dependentOn.oocytesRetrieved
                    )
                )
            )
        },
        oocytesNotUsed: {
            validator: helpers.withMessage(
                "Value must be equal to oocytesRetrieved - inseminatedInjected - oocytesCryopreserved",
                oocytesNotUsedValidator(
                    computed(
                        () => store.state.born.dependentOn.oocytesRetrieved
                    ),
                    computed(() => form.inseminatedInjected),
                    computed(
                        () => store.state.born.dependentOn.oocytesCryopreserved
                    ),
                    computed(() => store.state.artCycle.selectedCycle.cycleType)
                )
            )
        },
        reasonsForNoUse: {
            validator: helpers.withMessage(
                "Value Required",
                reasonsForNoUseValidator(computed(() => form.oocytesNotUsed))
            )
        },
        embryoStageFreezing: {
            validator: helpers.withMessage(
                "Value Required",
                embryoStageFreezingValidator(
                    computed(() => form.embryosCryopreserved)
                )
            )
        },
        inseminatedInjected: {
            validator: helpers.withMessage("Value Required", required)
        },
        normalFertilization2PN: {
            validator: helpers.withMessage(
                "Value Required and must be less or equal to oocytes inseminated/injected",
                normalFertilization2PNValidator(
                    computed(() => form.inseminatedInjected)
                )
            )
        },
        ofMII: {
            validator: helpers.withMessage(
                "Value Required",
                ofMIIValidator(computed(() => form.inseminatedInjected))
            )
        },
        utilizableEmbryos: {
            validator: helpers.withMessage(
                "Value Required",
                utilizableEmbryosValidator(
                    computed(() => form.inseminatedInjected)
                )
            )
        },
        embryosCryopreserved: {
            validator: helpers.withMessage(
                "Value Required & value must be less than # embryos Thawed",
                embryosCryopreservedValidator(
                    computed(() => form.inseminatedInjected),
                    computed(() => store.state.born.dependentOn.embryosThawed)
                )
            )
        },
        reasonEmbryoCryopreservation: {
            validator: helpers.withMessage(
                "Value Required",
                reasonEmbryoCryopreservationValidator(
                    computed(() => form.embryosCryopreserved),
                    computed(() => store.state.artCycle.selectedCycle.cycleDate)
                )
            )
        },
        embryoFreshCryoMethod: {
            validator: helpers.withMessage(
                "Value Required",
                embryoFreshCryoMethodValidator(
                    computed(() => form.embryosCryopreserved)
                )
            )
        },
        embryoFreezeDate1: {
            validator: helpers.withMessage(
                "Value Required",
                embryoFreezeDate1Validator(
                    computed(() => form.embryosCryopreserved)
                )
            )
        }
    };

    const v$ = useVuelidate(rules, form);

    const _save = () => {
        let payload = {
            ...form,
            ...store.getters["artCycle/getSelectedCycleAndPatientId"],
            embryoFreezeDate1: form.embryoFreezeDate1
                ? format(form.embryoFreezeDate1, DATE_FORMAT_STRING)
                : null,
            embryoFreezeDate2: form.embryoFreezeDate2
                ? format(form.embryoFreezeDate2, DATE_FORMAT_STRING)
                : null,
            embryoFreezeDate3: form.embryoFreezeDate3
                ? format(form.embryoFreezeDate3, DATE_FORMAT_STRING)
                : null,
            embryoFreezeDate4: form.embryoFreezeDate4
                ? format(form.embryoFreezeDate4, DATE_FORMAT_STRING)
                : null
        };

        // return;

        store.dispatch("born/updateFreshEmbryo", payload).then(() => {
            toast.add({
                severity: "success",
                summary: "",
                detail: "Save Successful",
                life: 3000
            });
        });
    };

    const confirm = useConfirm();

    const save = () => {
        if (v$.value.$invalid) {
            return;
        }
        emitter.emit("fresh-embryo:on-save");
        _save();
    };

    const fillForm = (data) => {
        form.inseminationMethod = data.inseminationMethod;
        form.oocytesNotUsed = data.oocytesNotUsed;
        form.reasonsForNoUse = data.reasonsForNoUse;
        form.embryoStageFreezing = data.embryoStageFreezing;
        form.inseminatedInjected = data.inseminatedInjected;
        form.embryoFreezeID1 = data.embryoFreezeID1;
        form.embryoFreezeID2 = data.embryoFreezeID2;
        form.embryoFreezeID3 = data.embryoFreezeID3;
        form.embryoFreezeID4 = data.embryoFreezeID4;
        form.normalFertilization2PN = data.normalFertilization2PN;
        form.embryoFreezeDate1 = data.embryoFreezeDate1
            ? new Date(data.embryoFreezeDate1 + TIME_0)
            : null;
        form.embryoFreezeDate2 = data.embryoFreezeDate2
            ? new Date(data.embryoFreezeDate2 + TIME_0)
            : null;
        form.embryoFreezeDate3 = data.embryoFreezeDate3
            ? new Date(data.embryoFreezeDate3 + TIME_0)
            : null;
        form.embryoFreezeDate4 = data.embryoFreezeDate4
            ? new Date(data.embryoFreezeDate4 + TIME_0)
            : null;
        form.ofMII = data.ofMII;
        form.utilizableEmbryos = data.utilizableEmbryos;
        form.embryosCryopreserved = data.embryosCryopreserved;
        form.reasonEmbryoCryopreservation = data.reasonEmbryoCryopreservation;
        form.embryoFreshCryoMethod = data.embryoFreshCryoMethod;
    };

    const fillFormDefault = (data) => {
        if (form.inseminationMethod.length == 0)
            form.inseminationMethod = data.inseminationMethod;
        if (form.oocytesNotUsed == null)
            form.oocytesNotUsed = data.oocytesNotUsed;
        if (form.reasonsForNoUse.length == 0)
            form.reasonsForNoUse = data.reasonsForNoUse;
        if (form.embryoStageFreezing.length == 0)
            form.embryoStageFreezing = data.embryoStageFreezing;
        if (form.inseminatedInjected == null)
            form.inseminatedInjected = data.inseminatedInjected;
        if (form.embryoFreezeID1 == null)
            form.embryoFreezeID1 = data.embryoFreezeID1;
        if (form.embryoFreezeID2 == null)
            form.embryoFreezeID2 = data.embryoFreezeID2;
        if (form.embryoFreezeID3 == null)
            form.embryoFreezeID3 = data.embryoFreezeID3;
        if (form.embryoFreezeID4 == null)
            form.embryoFreezeID4 = data.embryoFreezeID4;
        if (form.normalFertilization2PN == null)
            form.normalFertilization2PN = data.normalFertilization2PN;
        if (form.embryoFreezeDate1 == null)
            form.embryoFreezeDate1 = data.embryoFreezeDate1
                ? new Date(data.embryoFreezeDate1 + TIME_0)
                : null;
        if (form.embryoFreezeDate2 == null)
            form.embryoFreezeDate2 = data.embryoFreezeDate2
                ? new Date(data.embryoFreezeDate2 + TIME_0)
                : null;
        if (form.embryoFreezeDate3 == null)
            form.embryoFreezeDate3 = data.embryoFreezeDate3
                ? new Date(data.embryoFreezeDate3 + TIME_0)
                : null;
        if (form.embryoFreezeDate4 == null)
            form.embryoFreezeDate4 = data.embryoFreezeDate4
                ? new Date(data.embryoFreezeDate4 + TIME_0)
                : null;
        if (form.ofMII == null) form.ofMII = data.ofMII;
        if (form.utilizableEmbryos == null)
            form.utilizableEmbryos = data.utilizableEmbryos;
        if (form.embryosCryopreserved == null)
            form.embryosCryopreserved = data.embryosCryopreserved;
        if (form.reasonEmbryoCryopreservation == null)
            form.reasonEmbryoCryopreservation =
                data.reasonEmbryoCryopreservation;
        if (form.embryoFreshCryoMethod == null)
            form.embryoFreshCryoMethod = data.embryoFreshCryoMethod;
    };

    const getDeafultDataByPrimaryCycle = async ({ patientId, cycleId }) => {
        const resp = await getFreshEbmryo(patientId, cycleId);
        if (resp.status == "success") {
            fillFormDefault(resp.freshEmbryos);
        }
    };

    const inseminationMethodOptions = computed(
        () => store.state.options.inseminationMethod
    );
    const reasonsForNoUseOptions = computed(
        () => store.state.options.reasonsForNoUse
    );
    const embryoStageFreezingOptions = computed(
        () => store.state.options.embryoStageFreezing
    );
    const reasonEmbryoCryopreservationOptions = computed(
        () => store.state.options.reasonEmbryoCryopreservation
    );
    const embryoFreshCryoMethodOptions = computed(
        () => store.state.options.embryoFreshCryoMethod
    );

    const cycleIds = computed(() => store.state.artCycle.cyclesSummary);

    return {
        store,
        loading,
        form,
        v$,
        fillForm,
        save,
        inseminationMethodOptions,
        reasonsForNoUseOptions,
        embryoStageFreezingOptions,
        reasonEmbryoCryopreservationOptions,
        embryoFreshCryoMethodOptions,
        cycleIds,
        getDeafultDataByPrimaryCycle
    };
}

